.image-url-form {
  p {
    font-size: 1.2rem;
  }

  div {
    width: 70%;
    margin: 0 auto;
    padding: 30px;
    background: radial-gradient(
          circle farthest-side at 0% 50%,
          #fb1 23.5%,
          rgba(240, 166, 17, 0) 0
        )
        21px 30px,
      radial-gradient(
          circle farthest-side at 0% 50%,
          #b71 24%,
          rgba(240, 166, 17, 0) 0
        )
        19px 30px,
      linear-gradient(
          #fb1 14%,
          rgba(240, 166, 17, 0) 0,
          rgba(240, 166, 17, 0) 85%,
          #fb1 0
        )
        0 0,
      linear-gradient(
          150deg,
          #fb1 24%,
          #b71 0,
          #b71 26%,
          rgba(240, 166, 17, 0) 0,
          rgba(240, 166, 17, 0) 74%,
          #b71 0,
          #b71 76%,
          #fb1 0
        )
        0 0,
      linear-gradient(
          30deg,
          #fb1 24%,
          #b71 0,
          #b71 26%,
          rgba(240, 166, 17, 0) 0,
          rgba(240, 166, 17, 0) 74%,
          #b71 0,
          #b71 76%,
          #fb1 0
        )
        0 0,
      linear-gradient(90deg, #b71 2%, #fb1 0, #fb1 98%, #b71 0%) 0 0 #fb1;
    background-size: 40px 60px;
    box-shadow: 0 0 3px 1px black;
    border-radius: 5px;

    input {
      height: 40px;
      width: 75%;
      border: none;
      padding: 0 10px;
      outline: none;
    }
    button {
      border: none;
      height: 40px;
      width: 25%;
      background-color: #00cc00;
      color: white;
      padding: 0 10px;
      outline: none;
    }
  }
}
